import Axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const server = process.env.REACT_APP_BACKEND_URL;
class FormModalTimbanganBarcode extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listBarang: [],
      listBarangPerhiasan: [],
    };
  }

  componentDidMount() {
    Axios.get(server + "subcategorys2?kodeKelompok=CIOK")
      .then((res) =>
        this.setState({
          listBarang: res.data,
        })
      )
      .catch((err) => console.log(err));
    Axios.get(server + "subcategorys2?kodeKelompok=PERHIASAN")
      .then((res) =>
        this.setState({
          listBarangPerhiasan: res.data,
        })
      )
      .catch((err) => console.log(err));
  }

  handleChange = (input) => (e) => {
    var supp = e.target.value
      ? e.target.value
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
      kadarCetak: result_explode[1],
      kadarJual: result_explode[2],
    });
    this.props.change("kadarTransaksi", result_explode[2]);
    this.props.change("keterangan", result_explode[3]);
  };
  setNetto() {
    this.props.change(
      "netto",
      this.props.netto === "NaN" ? 0 : this.props.netto
    );
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="col-lg-12">
          <div className="row">
            <div className="col-lg-3">
              <label htmlFor="">Kode barang</label>
              <Field
                name="kodeBarang"
                className="form-control"
                component="select"
                onChange={this.handleChange()}
              >
                <option value="DEFAULT" key="DEFAULT">
                  SILAHKAN PILIH
                </option>
                {this.props.jenisBarang === "RONGSOK"
                  ? this.state.listBarang.map((list) => (
                      <option
                        value={
                          list.kodeJenis +
                          "|" +
                          list.kadarCetak +
                          "|" +
                          list.kadarJual +
                          "|" +
                          list.namaJenis
                        }
                        key={list.kodeJenis}
                      >
                        {list.namaJenis}
                      </option>
                    ))
                  : this.state.listBarangPerhiasan.map((list) => (
                      <option
                        value={
                          list.kodeJenis +
                          "|" +
                          list.kadarCetak +
                          "|" +
                          list.kadarJual +
                          "|" +
                          list.namaJenis
                        }
                        key={list.kodeJenis}
                      >
                        {list.namaJenis}
                      </option>
                    ))}
              </Field>
            </div>
            <div className="col-lg-6">
              <Field
                name="keterangan"
                label="Keterangan"
                type="text"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="kadarTransaksi"
                label="Kadar(%)"
                type="text"
                component={renderField}
                readOnly={true}
                onChange={this.setNetto()}
              />
            </div>
            <div className="col-lg-6">
              <label htmlFor="employed">Manual</label>
              <div>
                <Field
                  name="manual"
                  id="employed"
                  component="input"
                  type="checkbox"
                  value="MANUAL"
                  defaultChecked={true}
                />
              </div>
            </div>
            <div className="col-lg-6">
              <Field
                name="barcode"
                label="Barcode"
                type="text"
                component={renderField}
                readOnly={this.props.manual}
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="gross"
                label="Gross(Gr)"
                type="text"
                readOnly={!this.props.manual}
                component={renderField}
                onChange={this.setNetto()}
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="pkg"
                label="Package(Gr)"
                type="text"
                readOnly={!this.props.manual}
                component={renderField}
                onChange={this.setNetto()}
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="netto"
                label="Netto"
                type="text"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className="col-lg-3 d-none">
              <Field
                name="noFaktur"
                label="Netto"
                type="text"
                component={renderField}
                readOnly={true}
                onLoad={() =>
                  this.props.change("noFaktur", this.props.noFaktur)
                }
              />
            </div>
            <div className="col-lg-12">
              <div className="text-right">
                <button className="btn btn-primary" type="submit">
                  Simpan
                </button>
              </div>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

// Inisialisasi Redux Form
FormModalTimbanganBarcode = reduxForm({
  form: "FormModalTimbanganBarcode",
  enableReinitialize: true,
})(FormModalTimbanganBarcode);
const selector = formValueSelector("FormModalTimbanganBarcode"); // <-- same as form name
export default connect((state) => {
  const { gross, kadarTransaksi, pkg, manual } = selector(
    state,
    "gross",
    "kadarTransaksi",
    "pkg",
    "manual"
  );
  return {
    netto: (
      parseFloat(gross ? gross : 0) -
      parseFloat(pkg ? pkg : 0) * (parseFloat(kadarTransaksi) / 100)
    ).toFixed(3),
    manual: manual || false,
  };
})(FormModalTimbanganBarcode);
